@import url("https://fonts.googleapis.com/css?family=Inter:300,400,500,700&display=swap");
@import url("https://fonts.googleapis.com/css?family=Courier+Prime:400,700&display=swap");

$card_width: 15vh;
$gamepadding: 1em;

html {
  min-height: 100%;
}

body {
  /* padding: 1em; */
  font-family: "Inter", sans-serif;
  font-size: 18px;
  margin: 0;
  background: #038604;
  background-image: url("greenmat.png");
  color: white;
}

.waiting {
  cursor: wait !important;
}

input,
button,
div.toolbar span {
  display: inline-block;
  font-family: "Inter", sans-serif;
  font-size: 18px;
  line-height: 40px !important;
  outline: none;
  border: 2px solid black;
  border-radius: 4px;
  padding: 0 12px;
  vertical-align: middle;
  background: white;
  color: black;
  margin: 0;
  -webkit-appearance: none;
}

input {
  font-family: "Courier Prime", monospace;
  font-weight: 400;
  background: rgb(244, 244, 244);
  background: linear-gradient(
    0deg,
    rgba(244, 244, 244, 1) 0%,
    rgba(242, 238, 233, 1) 100%
  );
  padding-top: 4px;
  line-height: 36px !important;
  box-shadow: inset 5px 5px 10px -10px rgba(0, 0, 0, 0.7);
}

button.btn-error {
  background: darkred;
  color: white;
}

button.primary {
  background: #05652f;
  color: white;
}

button.cancel {
  background: rgb(113, 110, 107);
  color: white;
}

input:disabled,
button:disabled,
input:disabled:hover,
button:disabled:hover {
  color: #888 !important;
  background: #eee !important;
  cursor: default;
}

.waiting {
  *:hover {
    cursor: wait !important;
  }
}

button {
  padding: 0 14px;
}

button:hover {
  cursor: pointer;
  background: black;
  color: white;
}

h1 {
  font-weight: 400;
  margin: 0 0 12px 0;
  padding: 0;
}

div.error {
  background: pink;
  color: darkred;
  border: 2px solid darkred;
  padding: 20px;
  border-radius: 3px;
  overflow: visible;
  margin: 30px 20px auto 20px;
  box-shadow: 11px 10px 35px -14px rgba(0, 0, 0, 0.75);
}


.withtooltip .tooltip {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
 
  position: absolute;
  z-index: 1;
}

/* Show the tooltip text when you mouse over the tooltip container */
.withtooltip:hover .tooltip {
  visibility: visible;
}


div.notify-wrapper {
    position: absolute;
    height: 100%;
    width: 100%;
    margin: -#{$gamepadding};
    text-align: center;
    background-color: rgba(0, 0, 0, 0.2);
}

.notify {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: fit-content;
}

div.wrapper {
  background: white;
  color: black;
  border: 2px solid black;
  padding: 20px;
  border-radius: 3px;
  overflow: visible;
  margin: 30px 20px auto 20px;
  box-shadow: 11px 10px 35px -14px rgba(0, 0, 0, 0.75);
}

/* div.game,                                                    */
.box {
  background: white;
  border: 2px solid black;
  border-radius: 3px;
  padding: 8px;
  margin: 8px 0;
}

.box h2 {
  margin: -8px -8px 8px -8px;
  padding: 8px 10px;
  font-size: 15px;
  font-weight: 700;
  background: black;
  color: white;
}

.box ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

div.box li {
  margin: 0;
  padding: 0;
}

p {
  margin: 12px 0;
}

p.error {
  color: rgb(199, 10, 10);
  font-weight: 500;
}


div.toolbar * {
  margin: 8px 0 !important;
  border-top-right-radius: 0 !important;
  border-bottom-right-radius: 0 !important;
}

div.toolbar * + * {
  border-left: none !important;
  border-top-left-radius: 0 !important;
  border-bottom-left-radius: 0 !important;
}

div.toolbar *:last-child {
  border-top-right-radius: 4px !important;
  border-bottom-right-radius: 4px !important;
  border-right: 2px solid black !important;
}

div.toolbar span {
  background: #eee;
}

p.turn-info {
  float: right;
}

.game {
  display: grid;
  grid-template-rows: [header] 50px [players] 1fr [action] 1fr [hand] 1fr [bottom] 30px [end];
  grid-template-columns: [game] auto [chat] 400px [end];
  height:100%;
  padding: $gamepadding;
}

.header {
  grid-row-start: header;
  grid-column-start: game;
}

.game-table {
  grid-column-start: game;
  display: flex;
  flex-direction: column;
}

.players {
  grid-column-start: game;

  display: grid;
  grid-template-rows: 100%;
  justify-content: space-between;
  padding: 1vh;
  /* background-color:green; */
}


.player {
  grid-row-start: 1;
  width: $card_width;
  height: 27vh;
  margin: 2px;

  .nickname {
    // border: 2px solid black;
    border-radius: 3px;
    padding: 6px;
    margin: 6px 0;

    // background: black;
    background: linear-gradient(to right, black, rgba(0,0,0,0));
    color: white;

  }
}

/*
.role-taker {
    .nickname {
      border: 2px solid red;
      background: red;
      color: white;
    }
}

.role-partner {
    .nickname {
      border: 2px solid pink;
      background: pink;
      color: black;
    }
}
*/

.current-player {
    .nickname {
      border: 2px solid yellow;
    }
}

.actions {
  grid-column-start: game;
  grid-row-start: action;
  display:grid;
  justify-content:center;
  padding: 1vh;
  /* background-color:orange; */
}

section.hand {
  grid-row-end: bottom;
}

.hand{
  grid-column-start: game;
  display: grid;
  grid-template-columns: repeat(auto-fit,  minmax(10px, max-content)) ;
  justify-content: center;
  width: 90%;
  height: 27vh;
  margin-left: auto;
  margin-right: auto;
  min-width: 0;
  min-height: 0;
}

.card{
  width: $card_width;
  height: 28vh;
  background-image: var(--bg-image);
  background-size: $card_width 28vh;
  cursor: pointer;
  box-shadow: 5px 6px 15px -5px rgba(0, 0, 0, 0.75);
}

.hand > .card:hover{
  transform: scale(1.3);
}

.chat {
  grid-row-start: players;
  grid-row-end: bottom;
  grid-column-start: chat;

  background-color: lightgreen;
  color: black;
  font-family: "Courier Prime", monospace;
  overflow-y: auto;
  display:flex;
  flex-direction:column;
  max-height: 50vh;
}

table.scores {
  border-collapse: collapse;

  td, th {
    border: 1px solid #ddd;
    padding: 8px;
  }

  tr:nth-child(even){background-color: #f2f2f2;}

  tr:hover {background-color: #ddd;}

  th {
    padding-top: 8px;
    padding-bottom: 8px;
    text-align: left;
    background-color: black; 
    color: white;
  }
}


.yourturn {
  font-weight: bold;
    padding: 8px;
    background-color: black; 
    color: white;
}


.chat-messages {
  flex: 1;
  max-height: 95%;
  overflow: auto;
  display: flex;
  flex-direction: column-reverse;
}

